import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTag,  removeTag } from './actions'

class Tags extends React.Component{

	constructor(props) {
		super(props)

		this.handleChange = this.handleChange.bind(this)
		this.removeTag = this.removeTag.bind(this)
		this.addTag = this.addTag.bind(this)
		this.removeTag =this.removeTag.bind(this)

		this.state = {
			id : this.props.map((i) => {this.props.list[i].id}),
			tags : [],
		}

	}

	removeTag(e){
		e.preventDefault()
		this.setState
		this.props.removeTag(e.target.value)
	}

	addTag(e) {
		e.preventDefault()
		this.setState({
			tags : this.tags.push(this.state.tag)
		})
	}

	handleChange(event) {
		this.setState({
			tag: event.target.value
		})
	}

	render(){
		return(
			<div id="tags">
			<ul>
				{this.props.tags.tags.map((tag,i) => {
					return (
						<li key={i}>
							<button onClick={this.removeTag} value={tag} > {tag} </button>
							</li>
					)
				})}
			</ul>
				<form onSubmit ={this.addTag}>
					<input type="text"  autoComplete="off" name="tags" onChange={this.handleChange} placeholder="Add New Tag" />
				</form>


			</div>
		)
	}

}

Tags.propTypes = {
	tasks : PropTypes.object,
	list : PropTypes.array,
	tags : PropTypes.object,
	addTag: PropTypes.func,
	handleRemoveTag: PropTypes.func,
	handleChange : PropTypes.func,
	removeTag :PropTypes.func
}

export default connect(
	state => ({
		tags: state.tags
	}), {
		addTag,
		removeTag
	}
)(Tags)



