export function addTag(tagType) {
	return {
		type : 'ADD_TAG',
		tagType : tagType
	}
}

export function addTagToTask(tagList){
	return {
		type: 'ADD_TASK_TAG',
		tagList : tagList
	}
}

export function removeTag(tagType) {
	return {
		type : 'REMOVE_TAG',
		tagType : tagType
	}
}



